@use '../../../design' as *;
@use '../../../layout' as *;


@keyframes background-shadow {
    0% {
        box-shadow: calc(-1rem / 16) calc(11rem / 16) calc(7rem / 4) calc(-3rem / 16) rgba(0,0,0,1);
    }

    50% {
        box-shadow: 0 0 0 0 rgba(0,0,0,1);
    }

    100% {
        box-shadow: calc(-1rem / 16) calc(11rem / 16) calc(7rem / 4) calc(-3rem / 16) rgba(0,0,0,1);
    }
}


.menu-movil {
    
    &__link {
        
        @include position();
        @include flex-container(
            $justify-content: center,
        );

        a {
            cursor: auto;
            padding: 1vh;
            margin-right: 5vh;
            // width: max-content(auto, calc(60vw + 8vh));
            // height: max-content(auto, 10vh);
            border-radius: 1rem;
            background-color: #ffffff;
            text-decoration: none;
            @include flex-container();
        }
    }
    
