    &__svg {
        
        border-radius: 50%;
        cursor: pointer;
        margin-left: 5vh;
        background-color: #fff;
        background-image: linear-gradient(to right, transparent, transparent, rgb(183, 78, 145, 0.7), rgb(94, 66, 166, 0.7));
        background-size: 300% 100%;
        transition: background-position 600ms ease-in-out, transform 600ms ease-in-out;
        animation: background-shadow 2s ease-out infinite;
        @include flex-container(
            $justify-content: center,
        );

        svg {
            stroke: black;
            transition: stroke 800ms ease-in-out;
        }

        &:hover {
            background-position: 100% 0;
            transform: scale(1.4);
            svg {
                stroke: #fff;
            }
        }
    }
    
    &__text {
        
        width: 50vw;
        cursor: pointer;
        text-align: center;
        font-size: 3rem;
        font-family: 'Crimson Text', serif;
        background-color: #fff;
        background-image: linear-gradient(to right, #05e7f3, #4e46f3, #7d2ae7, rgb(183, 78, 145, 0.7), rgb(94, 66, 166, 0.7));
        background-size: 300% 100%;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        -moz-text-fill-color: transparent;
        transition: background-position 600ms ease-in-out;
        @include flex-item();

        // &--profile {
        //     color: #fff;
        //     border-radius: 1rem;
        //     background-image: linear-gradient(to right, transparent, transparent);
        //     font-size: 2.2rem;@include flex-item();
        // }

        &:hover {
            background-position: 100% 0;
            color: #fff;
        }
    }
}


